@import "../../global.scss";

.header {
  width: 100%;
  height: 70px;
  background-color: $orangeColor;
  color: white;
  padding: 20px 40px;
  position: fixed;
  top: 0;
  z-index: 3;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;

  .logo {
    color: inherit;
  }
  nav {
    ul {
      display: flex;
      list-style: none;

      li {
        margin-right: 20px;
        color: white;
      }
      
    }
  }
  .hamburger {
    display: none;
  }
}
@media (max-width: 650px) {
  .header {
    background-color: $orangeColor;
    color: white;
    .logo {
      color: inherit;
    }
    nav {
      display: none;
    }
    .hamburger {
      width: 32px;
      height: 25px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      span {
        width: 100%;
        height: 3px;
        background-color: white;
        transform-origin: left;
        transition: all 1.5s ease;
      }
      &.active {
        span {
          &:first-child {
            transform: rotate(45deg);
          }
          &:nth-child(2) {
            opacity: 0;
          }
          &:last-child {
            transform: rotate(-45deg);
          }
        }
      }
    }
  }
}
